import Index from "./js/";
export default {
  name: "Welcome",
  data() {
    return {
      // 静态资源
      pageData: Index.init, // 全屏数据
      fullPageOpts: {
        // fullPage
        start: 0,
        dir: "h", // 滚动方向，默认为v，垂直滚动 垂直滚动：v,水平滚动：h
        duration: 50,
        beforeChange: function(prev, next) {
          // console.log('beforeChange', prev, next);
        },
        afterChange: function(afterChange, next) {
          // console.log('beforeChange', prev, next);
        }
      }
    };
  },
  mounted() {
    // mounted挂载结束状态
    const _this = this;
    // console.group("------mounted挂载结束状态------");
    this.$nextTick(() => {
      // ···
    });
  },
  methods: {
    page() {},
    goIndex() {
      this.$router.push("/index");
    }
  }
};



