import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // Swiper default index
    sBaseIndex: 0
  },
  // 数据操作
  mutations: {
    // 切换欢迎页
    changeBaseIndex(state, index) {
      state.sBaseIndex = index;
    }
  },
  // 数据操作 / 计算
  getters: {
    // baseIndex: state => {
    //   return state.todos.filter(todo => todo.done)
    // }
  },
  // 异步操作
  actions: {
    // commitBaseIndex(state, index) {
    //   state.sBaseIndex = index;
    // }
  }
})




