import Index from "./js/";
// import components_name from "components_name";
export default {
  props: {
    // 检测类型 + 其他验证
    // number: {
    //   type: Number,
    //   default: 0,
    //   required: true,
    //   validator: function (value) {
    //     return value >= 0
    //   }
    // }
  },
  name: "App",
  data() {
    return {
      // 静态资源
      staticData: Index,
      isApp: false
    };
  },
  components: {
    // 注册组件
    // components_name
  },
  computed: {
    // init() {
    //   return 1+1;
    // }
  },
  watch: { // 数据监听
    watch_fn: function (val, oldVal) {
      console.log("new: %s, old: %s", val, oldVal);
    }
  },
  beforeCreate() {  // beforeCreate创建前状态
    const _this = this;
    console.group("------beforeCreate创建前状态------");
  },
  created() { // created创建完毕状态
    const _this = this;
    console.group("------created创建完毕状态------");
    // 判断是否手机访问
    _this.isApp = Index.isApp();
    // 刷新跳转首页
    _this.$router.push('/');
  },
  beforeMount() { // beforeMount挂载前状态
    const _this = this;
    console.group("------beforeMount挂载前状态------");
  },
  mounted() { // mounted挂载结束状态
    const _this = this;
    console.group("------mounted挂载结束状态------");
    this.$nextTick(() => {
      // ···
    })
  },
  beforeUpdate() { // beforeUpdate更新前状态
    const _this = this;
    console.group("beforeUpdate更新前状态===============》");
  },
  updated() { // updated更新完成状态
    const _this = this;
    console.group("updated更新完成状态===============》");
  },
  beforeDestroy() { // beforeDestroy销毁前状态
    const _this = this;
    console.group("beforeDestroy销毁前状态===============》");
  },
  destroyed() { // destroyed销毁完成状态
    const _this = this;
    console.group("destroyed销毁完成状态===============》");
  },
  methods: {
    init() {
      console.log("V Init");
    }
  }
};



