
import Index from "./js/";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
props: {









},
name: "Tabbar",
data() {
return {

tabItem: Index.tabItem
};
},
components: {

"v-tabbar": Tabbar,
"v-tabbar-item": TabbarItem
},
computed: {



},
watch: { // 数据监听
watch_fn: function (val, oldVal) {
console.log("new: %s, old: %s", val, oldVal);
}
},
beforeCreate() {  // beforeCreate创建前状态
const _this = this;

},
created() { // created创建完毕状态
const _this = this;

},
beforeMount() { // beforeMount挂载前状态
const _this = this;

},
mounted() { // mounted挂载结束状态
const _this = this;

this.$nextTick(() => {

})
},
beforeUpdate() { // beforeUpdate更新前状态
const _this = this;

},
updated() { // updated更新完成状态
const _this = this;

},
beforeDestroy() { // beforeDestroy销毁前状态
const _this = this;

},
destroyed() { // destroyed销毁完成状态
const _this = this;

},
methods: {
changeBaseIndex(index) {
var _this = this;
_this.$store.commit('changeBaseIndex', index);
},
iselected(idx) {
return this.$store.state.sBaseIndex == idx;
}
}
};



