'use strict'

export default {
  header: { // 头部
    title: '首页',
    style: {
      backgroundColor: '#3168c3'
    },
    leftOption: {
      backText: '', // 左侧按钮文字
      preventGoBack: true, // 是否阻止返回
      showBack: true // 是否现实左侧返回按钮
    },
    rightOption: {
      showMore: true // 是否显示更多按钮
    }
  },
  bottomMenu: { // 底部菜单
    title: '选择语言',
    menu: [{
      // icon: 'http://dn-placeholder.qbox.me/110x110/FF2D55/000',
      key: 'ZH',
      value: '中文',
      default: true
    }, {
      // icon: 'http://dn-placeholder.qbox.me/110x110/FF2D55/000',
      key: 'EN',
      value: 'English'
    }],
    menuSelectedColod: {
      color: '#09BB07' // 菜单选中颜色
    },
    leftText: '取消', // 左侧按钮文字
    rightText: '确定', // 右侧安妮文字
    showMenus: false, // 是否显示菜单
    Language: ""
  },
  toast: { // 提示框
    flag: false, // 是否显示
    text: '', // 提示文字
    width: '' // 宽度 - style
  }
}



