
import Index from "./js/";
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
props: {









},
name: "Header",
directives: {
TransferDom
},
data() {
return {

header: Index.header,
bottomMenu: Index.bottomMenu,
toast: Index.toast
};
},
components: {

"xheader": XHeader,
PopupHeader,
Popup,
Group,
Radio,
Toast
},
computed: {



},
watch: { // 数据监听
watch_fn: function (val, oldVal) {
console.log("new: %s, old: %s", val, oldVal);
}
},
beforeCreate() {  // beforeCreate创建前状态
const _this = this;

},
created() { // created创建完毕状态
const _this = this;

},
beforeMount() { // beforeMount挂载前状态
const _this = this;

},
mounted() { // mounted挂载结束状态
const _this = this;

this.$nextTick(() => {

})
},
beforeUpdate() { // beforeUpdate更新前状态
const _this = this;

},
updated() { // updated更新完成状态
const _this = this;

},
beforeDestroy() { // beforeDestroy销毁前状态
const _this = this;

},
destroyed() { // destroyed销毁完成状态
const _this = this;

},
methods: {
init() {
console.log("V Init");
},
bottomMenuOk () {
const _this = this;
_this.bottomMenu.showMenus = false;
_this.toast.text = "设置 [ "+_this.bottomMenu.Language+" ] 成功";
_this.toast.width = "12em";
_this.toast.flag = true;
console.log('Click: Botton [OK]');
},
bottomMenuCancel () {
const _this = this;
_this.bottomMenu.showMenus = false;
_this.toast.text = "取消设置";
_this.toast.width = "";
_this.toast.flag = true;
console.log('Click: Botton [Cancel]');
},
radioChange (key,val) {
const _this = this;
_this.bottomMenu.Language = val;
console.log(key,val);
}
}
};



