import Vue from 'vue'
import Router from 'vue-router'
// --
import Welcome from 'com/views/welcome' // 初始化欢迎页面
import Index from 'com/views/index' // 首页

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



